import React from 'react';
import styles from './Other.module.scss';
import OArray from './OArray';
import AOS from 'aos';
import '../../aos.css';

class Monuments extends React.Component {
    componentDidMount = () => {
        AOS.init();
    }
    render(){
        return(
            <>
                <div className={styles.all}>
                    <h1 className={styles.title}>Other interesting places</h1>
                    {
                        OArray.map(item => (
                            <div className={styles.description}>
                                <div data-aos="fade-left" className={styles.text}>
                                    <h1>{item.h1}</h1>
                                    <p>{item.text}</p>
                                </div>
                                <img data-aos="fade-right" src={item.src} />
                            </div>
                        ))
                    }
                </div>
            </>
        )
    }
}
export default Monuments;