import React from 'react';
import './App.css';
import Navbar from './components/Navbar/Navbar';
import {BrowserRouter , Route , Switch} from 'react-router-dom';
import Welcome from './components/Welcome/Welcome';
import Monuments from './components/Monuments/Monuments';
import Landscapes from './components/Landscapes/Landscapes';
import Other from './components/Other/Other';

class App extends React.Component {
  render(){
    return (
      <>
          
          <BrowserRouter>
          <div className="navbar">
            <Navbar />
          </div>
          
            <Switch>
                  <Route exact path="/angielski/" component={Welcome}/>
                  <Route exact path="/angielski/monuments" component={Monuments}/>
                  <Route exact path="/angielski/landscapes" component={Landscapes}/>
                  <Route exact path="/angielski/other" component={Other}/>
            </Switch>
          </BrowserRouter>
      </>
    )
  }
}
export default App;
