import React from 'react';
import styles from './Navbar.module.scss';
import {NavLink} from 'react-router-dom';

class Navbar extends React.Component {
    state = {
        navbar : false
    }

    componentWillMount = () => {
        setInterval(this.change,100);
    }
    change = () => {
        if(window.pageYOffset > 3){
            this.setState({navbar : true});
        } else {
            this.setState({navbar : false});
        }
    }
    render(){
       return(
            <>
                <ul className={this.state.navbar ? styles.nav2 : styles.nav}>
                    <li className={styles.mainLI}></li>
                    <li className={styles.otherlI}><NavLink strict activeClassName={styles.activeLink} className={styles.link} exact to="/monuments">Monuments</NavLink></li>
                    <li className={styles.otherlI}><NavLink strict activeClassName={styles.activeLink} className={styles.link} exact to="/landscapes">Landscapes</NavLink></li>
                    <li className={styles.otherlI}><NavLink strict activeClassName={styles.activeLink} className={styles.link} exact to="/other">Other interesting places</NavLink></li>
                </ul>
            </>
        )
    }
}

export default Navbar;